/* eslint-disable no-unused-vars */
import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { MDBDataTable } from "mdbreact"


import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Spinner,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
// import Select from "react-select"
// import axios from 'axios'
// import  { Redirect } from 'react-router-dom'
// // import $ from "jquery";

// import Flatpickr from "react-flatpickr"
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import conf from '../../../conf.js'

import Confirmer from '../../../components/Confirmer.js'
import  { Redirect } from 'react-router-dom'

class ListeCompte extends Component {


  constructor(props) {
    super(props)
    this.state = {
      users:[],
      userData:[],
      id:0,
      show:false,
      ok:"",
      page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
      <Spinner type="grow" color="success" />
   </div>,
    status:true
    }
    this.handleDetail.bind(this)
    this.supprimer.bind(this)
    this.handleModifier.bind(this)
  }
  componentDidMount() {
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };

    fetch(conf.url+"user/users",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      this.setState({status:false})
     try{
      if(data.length){
        this.setState({userData:data})

        data.map((element,idx)=>{
         var tab= {
           id: element.id,
           nom: element.username,
           prenom: element.prenom,
           adresse: element.adresse,
           email: element.email,
           telephone: element.telephone,
           role: element.roles.libelle,
           //typesystemeirrigue: element.nom,
        
           action:  <a> 
                      <i style={{cursor:"pointer"}} onClick={this.handleModifier.bind(this,element.id)} className="bx bx-pencil bx-sm text-primary mr-2"/> 
                      <i style={{cursor:"pointer"}} onClick={this.handleDetail.bind(this,element.id)} className="bx bx-info-circle bx-sm text-primary mr-2"/> 
                      <i style={{cursor:"pointer"}} onClick={this.supprimer.bind(this,element.id)} className="bx bx-trash  bx-sm text-danger "/> 
                    </a>,
           
         }
         //onClick={this.handleDetail.bind(this.bind,element.id)}
        
         this.setState({users:[...this.state.users,tab]})
   
        })
      }else{
        this.setState({users:[]})
      }
     }catch(error){
      this.setState({users:[]})
     }
        
  
      })
  }
  handleSupprimer=()=>{

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,

      },
    };
    
  
  
  fetch(conf.url+"user/users/delete/"+this.state.id, requestOptions)
    
    .then(data =>{console.log("enregitre avec succes vrai:",data)
     console.log("data send",data)
      this.setState({users:this.state.users.filter(element=>element.id!=this.state.id)})
      this.setState({show:false})
     
   } )
  }

  handleDetail=(id)=>{
    localStorage.setItem('idUser',id);
    this.setState({ok: <Redirect to='/detail-compte'/>});
  }
  toggled=()=>{
    this.setState({show:!this.state.show})
  }

  supprimer=(id)=>{
  
    this.setState({id:id,show:true})
    
  }

  handleModifier=(id)=>{
    localStorage.setItem('idUser',id);
    this.setState({ok: <Redirect to='/modifier-compte'/>});
    
  }

  render() {

    const data = {
      columns: [
        {
          label: "Nom",
          field: "nom",
          sort: "asc",
          width: 150,
        },
        {
          label: "Prénom",
          field: "prenom",
          sort: "asc",
          width: 270,
        },
        {
          label: "Adresse",
          field: "adresse",
          sort: "asc",
          width: 100,
        },
        {
          label: "Email",
          field: "email",
          sort: "asc",
          width: 100,
        },
        {
          label: "Télephone",
          field: "telephone",
          sort: "asc",
          width: 100,
        },
       
        {
          label: "Rôle",
          field: "role",
          sort: "asc",
          width: 100,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        },
      ],
      rows:this.state.users
      
    }


    const dataRoles = {
      columns: [
        {
          label: "Rôle",
          field: "role",
          sort: "asc",
          width: 150,
        },
        
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        },
      ],
      rows:this.state.role
      
    }


    return (
      <React.Fragment>
        <div className="page-content"> 
          {this.state.ok}
        <Confirmer show={this.state.show} handleValider={this.handleSupprimer} toggle={this.toggled}/>


          <Container fluid={true}>
          {
          (!this.state.status)?

          (
            <>
            <Row>
              <Col md="12" className="pull-right">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col md="8">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h1 className="mb-0 font-size-18">Liste des comptes</h1>
                        </div>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-compte"
                        >
                          <i class='bx bx-plus-circle' > </i>  Créer un nouveau compte
                        </Button>
                      </Col>
                    </Row>

                    <hr/>

                    <Row ClassName="mt-5">
                      <Col md="12">
                        <MDBDataTable responsive striped bordered data={data} />
                      </Col>
                    </Row>

                  </CardBody>

                </Card>
              </Col>
            </Row>


            <Row className="mt-3">
              <Col md="12" className="pull-right">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col md="8">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h1 className="mb-0 font-size-18">Liste des rôles</h1>
                        </div>
                      </Col>

                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-role"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un nouveau rôle
                        </Button>
                      </Col>
                    </Row>

                    <hr/>

                    <Row ClassName="mt-5">
                      <Col md="12">
                        <MDBDataTable responsive striped bordered data={dataRoles} />
                      </Col>
                    </Row>

                  </CardBody>

                </Card>
              </Col>
            </Row>
             </>    
            ):(<>{this.state.page}</>)
            }
            
          </Container>
        </div>
        
      </React.Fragment>
    )
  }
}
export default ListeCompte